body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



html,body{
    color:whitesmoke;
    text-align: center;
    background-color: #000;
    font-family: 'Roboto', sans-serif;
    font-size: 1.3em;
    background-image: url(/Weather-Updates/static/media/weather-background.bb76056a.jpg);
    background-size: 100%; 
    background-repeat: no-repeat;
    box-shadow: rgba(0, 0, 0, 0.7) 0px 0px 0px 1000px inset;
    background-attachment: fixed;
    margin: 0;
    padding: 0;

}

nav{
    padding: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}


@media only screen and (max-width: 600px) {
    html, body {
        font-size: 1.1em;
        margin: 0;
        padding: 0;
    }
    nav{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}


.body{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

@media only screen and (max-width: 600px) {  
    .body{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}

@media only screen and (max-width: 600px) {
    .input{
        width: 70vw;
        margin-top:5px;
    }
}

.days{
    height: 530px;
    width: 415px;
    overflow: hidden;
    border-radius: 7px;
    margin: 60px 15px;
    transition: .7s all cubic-bezier(0.785, 0.135, 0.15, 0.86);
    box-shadow: 0 50px 150px -20px rgba(0,0,0,.75);
    color: white;
    background-image: url(/Weather-Updates/static/media/weather-background.bb76056a.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    font-family: 'Courier New', Courier, monospace;
}

@media only screen and (max-width: 600px) {
    .days{
        height: 470px;
        margin-top: 20px;
        margin-left:8px;
        margin-right: 0px;
    }
}

.current-day{
    height: 650px;
    overflow: hidden;
    border-radius: 7px;
    margin: 60px 15px;
    transition: .7s all cubic-bezier(0.785, 0.135, 0.15, 0.86);
    box-shadow: 0 50px 150px -20px rgba(0,0,0,1);
    color: white;
    font-family: 'Courier New', Courier, monospace;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}

@media only screen and (max-width: 600px) {
    .current-day{
        margin:0;
        height: 480px;
    }
}

.days:hover,.current-day:hover{
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-radius: 10px;
    box-shadow: 0 20px 300px -20px rgba(0,0,0,1);
}

.details{
    margin-top:20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-content: center;
            align-content: center;
    border-radius: 7px;
}

.main{
    margin: 10px 10% 0 10%;
}

@media only screen and (max-width: 600px) {
    .main{
        margin:0;
    }
}

button {
    padding: 10px 20px;
    transition: all 0.5s;
    border: 0 none;
    border-radius: 5em;
    font-size: 18px;
    margin-right: 20px;
    box-shadow: 2px 3px 6px rgb(43, 42, 42);
}

@media only screen and (max-width: 600px) {
    button {
        font-size: 14px;
        margin-right: 20px;
        margin-top:10px;
        padding:5px 5px;
        width: 120px;
    }
}

.btn{
    transition: all 0.5s;
    border: 0 none;
    border-radius: 5em;
    font-size: 14px;
    margin-left: 10px;
    outline: none;
    background-color: #fff;
    width:120px;
}

button:active,button:focus {
    background: rgb(114, 47, 2);
    color: white;
    outline: none;
}


.action {
    background-size: 0% 100%;
}


.action:hover {
    background-size: 100% 100%; 
    cursor: pointer;
    outline: none;
    color: white;
    background-image: url(/Weather-Updates/static/media/weather-background.bb76056a.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 0px 1000px inset;
}


.datas{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 430px;
    margin-left:100px;
}

@media only screen and (max-width: 600px) {
    .datas{
        font-size: 18px;
    }
    img{
        height:150px;
        width:150px;
    }
}


.left {
    width: 120px; 
    text-align: left;
    color:  rgb(253, 186, 1);
}

.middle {
    width: 20px;
    text-align: left;
    color:  rgb(253, 186, 1);
}
.right{
    width: 310px;
    text-align: left;
}

.date-time{
    margin: 10px 0 10px 0;
    font-size: 24px;
    color: white;
    font-family: 'Roboto', sans-serif;
    border:1px solid white;
    border-radius: 20px;
    background: rgb(114, 47, 2);
}

@media only screen and (max-width: 600px) {
    .date-time{
        font-size: 20px;

    }

}

.current-day-temp{
    margin: 10px 0 20px 0;
    font-size: 24px;
    color: white;
    font-family: 'Roboto', sans-serif;
    border:1px solid white;
    border-radius: 20px;
    background: rgb(114, 47, 2);
    padding: 10px;
}

@media only screen and (max-width: 600px) {
    .current-day-temp{
        font-size: 22px;
        padding: 3px 30px;
        -webkit-align-self: center;
                align-self: center;
    }
}

.day-night-temp, .max-min-temp{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top:20px;
}

.max-min-temp{
    margin-bottom: 20px;
}


.location{
    color: white;
    margin-top:30px;
    padding: 5px;
}

.input{
    text-align: center;
    border-radius: 5px;
    outline: none;
}


h1{
    color:  rgb(253, 186, 1);
    text-shadow: 2px 2px 4px rgb(245, 229, 0);
    font-size: 60px;
}

@media only screen and (max-width: 600px) {
    h1{
        font-size: 30px;
        text-shadow: 1px 1px 4px rgb(245, 229, 0);
    }
}

.main-table{
    margin:0 5% 0 5%;
}

@media only screen and (max-width: 600px) {
    .main-table{
        margin:0;
        width:100vw;
    }
}

table{
    border: 1px solid white;
    background-color: transparent;
}

@media only screen and (max-width: 600px) {
    table{
        width:100vw;
    }
}


th{
    font-weight: 500;
    font-size: 18px;
    color:  rgb(253, 186, 1);
    text-transform: uppercase;
    background-color: transparent;
}

@media only screen and (max-width: 600px) {
    th{
        font-weight: 300;
        font-size: 14px;
    }
}

tr {
    font-weight: 400;
    font-size: 19px;
    color: #fff;
    background-color: transparent;

}


@media only screen and (max-width: 600px) {
    tr{
        font-weight: 300;
        font-size: 14px;
    }
}

.made-with-love {
    margin-top: 40px;
    padding: 10px;
    clear: left;
    text-align: center;
    font-size: 14px;
    font-family: arial;
    color: #fff;
}

.made-with-love i {
    font-style: normal;
    color: #F50057;
    font-size: 14px;
    position: relative;
    top: 2px;
}

.table-content{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-content: center;
            align-content: center;
}

.weather-description{
    text-transform: capitalize;
}

.weather-details{
    margin-left: -70px;
}

@media only screen and (max-width: 600px) {
    .weather-details{
        margin-left: -60px;
        width:415px;
    }
}

